@import './../../scss/all.scss';

.publico {
    // background-color: #e4f0e8;
    min-height: 600px;
    padding: 80px 0;

    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    flex-direction: column;
    overflow: hidden;
    gap: 20px;

    .default-title {
        opacity: 0;
    }

    // &::before {
    //     content: '';
    //     width: 120%;
    //     height: 80%;
    //     top: 0;
    //     position: absolute;
    //     background-color: $color-1;
    //     transform: rotate(5deg);
    // }

    .container {
        gap: 40px;
        flex-direction: row;
        align-items: flex-start;

        @include mobile {
            display: flex;
            gap: 30px;
        }
    
    }

    &__logo {
        position: absolute;
        left: 50%;
        transform: translate(-50%, -70%);
        height: 200px;
        width: 200px;
        top: 50%;
        background-color: #e6e9f0;
        border-radius: 1000px;
        z-index: 11;

        padding: 30px;
        box-shadow: 0 4px 6px 0 rgba(12, 0, 46, 0.06);
        display: flex;
        align-items: center;
        justify-content: center;
        
        img {
            width: auto;
            height: 100%;
        }
    }


    &__modal {
        opacity: 0;
        width: 100%;
        z-index: 10;
        background-color: $color-7;
        border-radius: 16px;
        padding: 26px;
        height: 750px;

        display: flex;
        align-items: center;
        justify-content: flex-start;
        flex-direction: column;


        h3 {
            font-size: 28px;
            text-transform: uppercase;
            margin-bottom: 30px;
            color: $color-4;
            // color: white;
            width: 100%;
            text-align: center;
        }
        
        & > p {
            // color: white;
            font-size: 18px;
            min-height: 70px;
            line-height: 22px;
            margin-bottom: 20px;
        }

        img {
            height: 180px;
            width: auto;
        }

        ul {
            width: 100%;
            display: flex;
            flex-direction: column;
            margin-top: 50px;
            gap: 10px;
        }
        
        li {
            // list-style: inside;
            font-size: 16px;
            line-height: 22px;
            letter-spacing: .5px;
            // color: white;
            display: flex;
            align-items: center;
            justify-content: flex-start;
            gap: 10px;

            &:first-child {
                font-size: 20px;
                font-weight: 700;
                color: $color-4;
            }

            img {
                width: 20px;
                height: 20px;
            }

            b {
                color: $color-4;
            }

            p {
                height: auto;
            }
        }
    }

    &__img {
        width: 100%;
        height: 650px;
        overflow: hidden;
        border-radius: 16px;

        background-position: center;
        background-size: cover;

        img {
            height: 100%;
            width: auto;
        }
    }




    &.animate {
        .default-title {
            @include fade("in","right", 300px, 3s);
        }

        .publico__modal {
            @include fade("in","right", 300px, 3s, 200ms);
        }
    }

}